.app {
  box-sizing: border-box;
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.header {
  box-sizing: border-box;
  //max-height: 375px;
  //height: 61vw;
  background-color: #FFBC53;
}

.card {
  display: flex;
  justify-content: space-between;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  color: white;
  padding: 16px;

  &__weather {
    font-size: 48px;
    line-height: 56px;
  }

  &__condition {
    font-size: 24px;
    line-height: 28px;
  }
}

.term {
  font-size: 18px;
  line-height: 21px;
  font-weight: normal;
}

.forecast-day {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  color: white;
  background-color: #AEBFD1;
  margin-bottom: 1px;
  box-shadow:  0px 1px 2px rgba(0, 0, 0, 0.25);

  &__header {
    box-sizing: border-box;
    display: flex;
    font-size: 24px;
    line-height: 28px;
    padding-left: 16px;
    height: 64px;
    box-shadow:  0px 1px 2px rgba(0, 0, 0, 0.25);
  }

  &__column {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 50%;
  }

  &__arrow {
    width: 12px;
    height: 12px;
    margin-right: 12px;
  }

  &__icon {
    width: 33.3%;
  }

  &__max {
    width: 33.3%;
  }

  &__min {
    width: 33.3%;
  }
}

.arrow-enter {
  transform: rotate(0deg);
}

.arrow-enter-active {
  transform: rotate(180deg);
  transition: transform linear 300ms;
}

.arrow-enter-done {
  transform: rotate(180deg);
}

.arrow-exit {
  transform: rotate(180deg);
}

.arrow-exit-active {
  transform: rotate(360deg);
  transition: transform linear 300ms;
}

.forecast__row-full {
  box-sizing: border-box;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

.row-full-enter {
  max-height: 0;
  opacity: 0;
}

.row-full-enter-active {
  max-height: 180px;
  overflow: hidden;
  opacity: 1;
  transition: 300ms linear;
}

.row-full-exit {
  max-height: 180px;
  opacity: 1;
}

.row-full-exit-active {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: 300ms linear;
}

.row__item {
  display: flex;
  align-items: center;
}